body, #root {
  width: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container__content {
  width: 75%;
  align-items: center;
  align-content: center;
  align-self: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.newNote__form {
  display: flex;
  justify-content: center;
  width: 30%
}

.newNote__input[type=text] {
  border: none;
  width: 75%;
  border-radius: 0;
  border-bottom: 1px solid black;
  margin-bottom: -1px;
  align-self: center;
}

.newNote__input[type=text]:focus {
  border-bottom: 2px solid black;
}

.note {
  list-style: none;
  color: rgb(0, 0, 0);
  padding-top: 15px;
  font-size: 15px;
}

button {
  border: 2px;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
}

.btn__collapse-btn {
  width: 30%;
}

.btn__collapse-btn:hover {
  color: white;
  background: rgb(10, 88, 206);
}

.btn__add-btn {
  color: black;
  padding: 0.3rem 0.5rem;
}

.btn__add-btn:hover {
  color: white;
  background: rgb(23, 167, 30);
}

.btn__delete-btn:hover {
  color: white;
  background: rgb(253, 37, 37);
}

.note__importance-btn__Important:hover {
  color: white;
  background: rgb(10, 88, 206);
}

.note__importance-btn__Important:hover span {
  display:none
}
.note__importance-btn__Important:hover:before {
  content:"Mark as not important?"
}

.note__importance-btn__Not_Important:hover {
  color: white;
  background: rgb(10, 88, 206);
}

.note__importance-btn__Not_Important:hover span {
  display:none
}
.note__importance-btn__Not_Important:hover:before {
  content:"Mark as important?"
}
